import React, { useState } from "react";
import { Link } from "react-router-dom";
import DataTable from "react-data-table-component";
import './Home.css';
import home1 from './img/header1.png';
import home2 from './img/header2.jpg';

function Home() {

    const [nutrition, setNutrition] = useState(false);
    const handleClick = (nutri) => {
        var tabela = document.querySelector(".nutrition");
        var detalhes = document.querySelector(".details");

        if (nutri == true) {
            tabela.classList.add("active");
            detalhes.classList.remove("active");
        } else {
            detalhes.classList.add("active");
            tabela.classList.remove("active");
        }
    }
    const data = [

        { Ingredientes: "Valor Energtico (kcal):", Quantidade: 4.4 },

        { Ingredientes: "Carboidrator (g):", Quantidade: 2.4 },

        { Ingredientes: "Acares totais (g):", Quantidade: 0 },

        { Ingredientes: "Acares adicionados (g):", Quantidade: 0 },

        { Ingredientes: "Poliois totais (g):", Quantidade: 1 },

        { Ingredientes: "Eritritol (g):", Quantidade: 1 },

        { Ingredientes: "Sdio (mg):", Quantidade: 78 },

        { Ingredientes: "Vitamina B3 (mg):", Quantidade: 15 },

        { Ingredientes: "Vitamina B5 (mg):", Quantidade: 5 }

    ]
    const columns = [
        {
            name: "Ingredientes", selector: "Ingredientes", sortable: false,
        },
        {
            name: "Quantidade", selector: "Quantidade", sortable: false
        }
    ];





    return (
        <div className="page-content">
            <div className="background">
                <div className="row page1">
                    <div class="col-12 d-flex justify-content-center mt-5 img1" >
                        <img src={home1} class="img" width="100" />

                    </div>
                    <div class="col-12 d-flex justify-content-center mt-5 img1" >
                        <button class="button-over-img btn btn--inverse" type="button" >
                            <Link to="https://www.instagram.com/monsterenergy/?hl=pt-br" className="buttonText" >
                                <p>UNLEASH THE BEAST</p>
                            </Link>
                        </button>


                    </div>

                    <div class="col-12 d-flex justify-content-center mt-5 img1" >
                        <img src={home2} class="img watermelon" />
                        <div class="col-7 detail-desktop" >
                            <div class="detail-desktop-text">
                            <h1>UNLEASH THE ULTRA BEAST!</h1>
                            <p>Sob o cu noturno iluminado por fogos de artifcio, voc tem sua paixo ao seu lado. Com boa msica e melhores amigos,  o melhor vero de todos. Ultra Watermelon  o vero na lata, ento voc pode aproveit-lo a qualquer hora. Zero acar, sabor refrescante com a mistura explosiva de energia Monster para iluminar as noites quentes de vero.</p>
                            </div>
                        </div>
                        
                    </div>
                    <div class="row col-12 d-flex justify-content-center mt-3  datatable-desktop img1" >
                            <DataTable
                                columns={columns}
                                data={data}
                            />
                        </div>

                    <div class="mobile-screen">
                        <div class="col-12 mb-3 d-flex justify-content-between details-mobile" >
                            <button class="buttonDetails1 col-4 " type="button" onClick={e => handleClick(true)} >
                                <p>TABELA</p>
                            </button>
                            <button class="buttonDetails2 col-4" type="button" onClick={e => handleClick(false)}>
                                <p>DETALHES</p>
                            </button>


                        </div>
                        <div class="col-12 nutrition active" >
                            <DataTable
                                columns={columns}
                                data={data}
                            />
                        </div>



                        <div class="col-12 details" >
                            <h1>UNLEASH THE ULTRA BEAST!</h1>
                            <p>Sob o cu noturno iluminado por fogos de artifcio, voc tem sua paixo ao seu lado. Com boa msica e melhores amigos,  o melhor vero de todos. Ultra Watermelon  o vero na lata, ento voc pode aproveit-lo a qualquer hora. Zero acar, sabor refrescante com a mistura explosiva de energia Monster para iluminar as noites quentes de vero.</p>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    )
}

export default Home;